$font-header: 'Cormorant Garamond', 'Georgia', serif;
$font-text: 'Poppins', 'Arial', sans-serif;

@font-face {
  font-family: 'Poppins';
  src: url('../fonts/poppins/poppins-medium.woff2') format('woff2'),
      url('../fonts/poppins/poppins-medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

